___CSS_LOADER_EXPORT___.push([module.id, `* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: "Lato", sans-serif;
  background-color: #f5f5f5;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 80vh;
  font-size: 1.25rem;
}

.todo-container {
  position: relative;
  box-shadow: 0 3px 10px rgba(14, 13, 13, 0.2);
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: #fff;
  margin: 0 auto;
  width: 60%;
}

.todo-header,
form {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 1rem;
  border-bottom: 2px solid #f1f1f1;
}

.todo-header {
  padding: 1.5rem 1rem;
}

.todo-list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  margin: 0 auto;
  overflow: hidden;
}

.todo-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 1rem;
  gap: 1.5rem;
  border-bottom: 2px solid #f1f1f1;
}

.todo-item .description {
  width: 100%;
}

.completed-task {
  text-decoration: line-through;
}

input {
  border: none;
  padding: 0.8rem;
  outline: none;
  flex-grow: 4;
  background-color: transparent;
}

.clear-todo {
  background-color: #f5f5f5;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  outline: none;
  cursor: pointer;
}

.clear-todo:hover {
  text-decoration: underline;
}

form > input {
  font-style: italic;
  font-size: 1.2rem;
}
